@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap");

* {
  box-sizing: border-box;
}

body {
  background-color: rebeccapurple;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins", sans-serif;
  margin: 0;
  min-height: 100vh;
}

.pw-container {
  background: #0a0e31;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  min-width: 400px;
}

.pw-header {
  padding: 1rem;
}

.pw {
  background: rgba(255, 255, 255, 0.08);
  display: flex;
  font-size: 1.5rem;
  align-items: center;
  height: 70px;
  width: 100%;
  position: relative;
  padding: 1rem;
  overflow: auto;
}

.pw button {
  background-color: white;
  border: none;
  color: rgb(29, 2, 56);
  cursor: pointer;
  font-family: inherit;
  font-weight: bold;
  padding: 0.25rem;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(0, 20%);
  transition: opacity 0.2s ease, transform 0.2s ease;
  opacity: 0;
}

.pw:hover button {
  opacity: 1;
  transform: translate(0, 0%);
}

.pw-body {
  padding: 0 1rem 1rem;
}

.form-control {
  background: rgba(255, 255, 255, 0.08);
  position: relative;
  color: #eee;
  display: flex;
  justify-content: space-between;
  margin: 0.75rem 0;
  padding: 10px 25px;
}

.form-control__checkbox label::before,
.form-control__checkbox label::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 10px;
  cursor: pointer;
  transform: translateY(-50%);
}

.form-control__checkbox input {
  position: absolute;
  opacity: 0;
  z-index: -1;
}

.form-control__checkbox label::before {
  height: 30px;
  width: 50px;
  border-radius: 30px;
  background: rgba(214, 214, 214, 0.434);
}

.form-control__checkbox input:checked + label:before {
  background: #5d68e2;
}

.form-control__checkbox input:checked + label:after {
  right: 14px;
}

.form-control__checkbox label::after {
  height: 24px;
  width: 24px;
  border-radius: 60px;
  right: 32px;
  background: #fff;
  transition: all 150ms cubic-bezier(0, 0, 0, 0.1);
}

.generate {
  background-image: linear-gradient(135deg, #667eea 0%, rebeccapurple 100%);
  border: none;
  color: #fff;
  cursor: pointer;
  display: block;
  font-size: 1.5rem;
  font-weight: bold;
  padding: 0.75rem;
  margin-top: 1rem;
  width: 100%;
}
